.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.picture {
  text-align: center;
}

.container-div{ 
  Display: flex;
  Height: 50%;
  Width: 50%; 
  Align-Items: center;
  Justify-Content: center;
}

.image_center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}
.image_caption {
  text-align: center;
  font-size: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  margin: 25px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
